.bts-opt-in {
  width: 450px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  margin-top: 30px;
  .content {
    p {
      font-size: 18px;
    }
  }
  .actions {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: #dadada;
  }
  .what-is-granted > p {
    font-size: 16px;
    margin: 10px 0;
    cursor: pointer;
    color: blue;
    text-align: center;
  }
  .permissions {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px 0;
    // display: none;
    margin-bottom: 12px;
    &.show {
      display: flex;
    }
    .permission {
      display: flex;
      align-items: center;
      gap: 10px;
      i {
        font-size: 50px;
        width: 50px;
        height: 50px;
      }
      h3 {
        margin: 3px;
        font-size: 14px;
      }
      p {
        margin: 3px;
        font-size: 14px;
      }
    }
  }
  .privacy-policy {
    padding: 10px;
    background: #dadada;
    text-align: center;
    display: flex;
    gap: 10px;
    justify-content: center;
  }
  .bp-connect {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    margin: 20px 0;
    align-items: center;
    img,
    .wp-icon {
      border: 1px solid #ddd;
      padding: 1px;
      background-color: #fff;
      height: 80px;
      width: 80px;
    }
    > i {
      font-size: 40px;
      width: 40px;
      height: 40px;
      color: #0004;
    }
    .wp-icon i {
      font-size: 64px;
      background: #01749a;
      color: #fff;
      width: 64px;
      height: 64px;
      padding: 8px;
    }
  }
}
#opt_in_modal.modal_sdk {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: #0005;
  top: 0;
  left: 0;
  display: block;

  .opt_in_content {
    width: 500px;
    background: #fff;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    .content {
      display: flex;
      gap: 40px;
      h3 {
        font-size: 25px;
      }
      p {
        font-size: 18px;
      }
      img {
        width: 150px;
      }
    }
    .button {
      width: 100%;
    }
    .granted-info {
      display: block;
      ul {
        padding-left: 30px;
        list-style: disc;
      }
    }
    .learn-skip {
      display: flex;
      gap: 10px;
      justify-content: center;
      a {
        text-decoration: none;
      }
      .hide-btn {
        display: none;
      }
    }
  }
  a {
    text-decoration: none;
  }
}
